.ticket {
  &Description {
    display: flex;
    flex-direction: column;
    background: #ffffff;
    row-gap: 10px;
    height: 184px;
    border-radius: 5px;
    justify-content: center;
  }

  &List {
    display: flex;
  }

  &Item {
    display: flex;
    flex-direction: column-reverse;
    flex-basis: 30%;
    flex-grow: 1;
    justify-content: center;
    align-items: center;
    row-gap: 2px;
    font-family: 'Open Sans SemiBold', sans-serif;
    font-size: 12px;
  }

  &Info {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &Container {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
  }

  &Name {
    color: #a0b0b9;
  }

  &Price {
    font-family: 'Open Sans SemiBold', sans-serif;
    font-size: 24px;
    color: #2196f3;
  }

  &Header {
    padding: 0 40px;
    display: flex;
    justify-content: space-between;
  }

  &Img {
    width: 110px;
    height: 36px;
  }
}
